.Footer {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 48px 0;
  width: 100%;
  background-color: #9D8855;
  color: #fff; }

@media screen and (max-width: 768px) {
  .Footer {
    flex-direction: column; } }

.Footer__brand {
  width: 150px; }

.Footer__copy {
  font-size: 14px;
  margin: 0;
  opacity: .8;
  font-weight: lighter; }

@media screen and (max-width: 768px) {
  .Footer__copy {
    font-size: 12px; } }

.Footer hr {
  width: 1.3px;
  height: 70px;
  border: none;
  background-color: rgba(255, 255, 255, 0.437);
  margin: 0 30px 0 12px; }

@media screen and (max-width: 768px) {
  .Footer hr {
    width: 270px;
    margin: 20px 0;
    height: 1px; } }
