.CSSTransition--fade-enter {
  position: absolute;
  opacity: 1;
  z-index: 3; }

.CSSTransition--fade-enter-active {
  position: absolute;
  opacity: 1;
  z-index: 3; }

.CSSTransition--fade-exit {
  position: absolute;
  pointer-events: none;
  opacity: 1;
  z-index: 6; }

.CSSTransition--fade-exit-active {
  position: absolute;
  opacity: 0;
  -webkit-transition: opacity 500ms;
  transition: opacity 500ms;
  z-index: 6; }

.CSSTransition--modal-enter {
  top: -50vh;
  opacity: 0; }

.CSSTransition--modal-enter-active {
  opacity: 1;
  top: 20%;
  -webkit-transition: all 200ms;
  transition: all 200ms; }

.CSSTransition--modal-exit {
  position: absolute;
  opacity: 1;
  top: 20%; }

.CSSTransition--modal-exit-active {
  opacity: 0;
  top: -50vh;
  -webkit-transition: all 200ms;
  transition: all 200ms; }

.transition-item {
  -webkit-transition: opacity 800ms;
  transition: opacity 800ms; }

.transition-item.transition-appear {
  opacity: 1; }

.transition-item.transition-appear.transition-appear-active {
  opacity: 1; }

.transition-item.transition-leave {
  opacity: 1; }

.transition-item.transition-leave.transition-leave-active {
  opacity: 0; }

.transition-wrapper {
  position: relative;
  z-index: 5; }

.transition-wrapper .transition-item {
  position: fixed;
  top: 0;
  right: 0;
  left: 0; }
